import React,{useEffect} from 'react';
import alanBtn from '@alan-ai/alan-sdk-web';

const alanKey='742ddb1f4ae5b3c016b10e5d65deda982e956eca572e1d8b807a3e2338fdd0dc/stage';

const App=()=>{

    useEffect(() => {
        alanBtn({
            key: alanKey,
            onCommand: ({command})=>{
                if(command === 'testCommand'){
                    alert('this code was executed');
                }
            }
        })
    },[])
    return(
        <div>
            <h1>News Website for Blind</h1>
            <p> Try asking 'What does this app do?' or 'What can i do here'</p>
        </div>
    );
}

export default App;